#header {
  width: var(--side-bar-width);
  height: 100%;
  padding: 4em 4em;
  position: fixed;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  text-align: left;
  background-color: #333;
  font-size: var(--main-font-size);
}

#header * {
  color: rgba(255, 255, 255, 0.5);
}

#header *:hover {
  color: #f5f5f5;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

#header .blog-avatar {
  margin-bottom: 1em;
  width: 16em;
  height: 16em;
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center;
  border-radius: 100%;
}

#header > .inner {
  margin: 0 0 0 0;
}

#header .blog-title {
  font-size: 2.2em;
  line-height: 2em;
  color: #f5f5f5;
}

#header .blog-subtitle {
  font-size: 1.8em;
  line-height: 2em;
  color: #2f6ce6;
}

#header .blog-title,
#header .blog-subtitle,
#header .blog-desc {
  font-weight: 400;
}

#header .blog-desc {
  font-size: 1em;
  line-height: 1.5em;
}

.block-blank {
  height: 20em;
}

#footer .links {
  margin-bottom: 2em;
}

#footer .links li {
  margin: 0;
  list-style: none;
}

#footer .links .link {
  font-size: 2em;
  line-height: 2em;
}

#footer .copyright {
  font-size: 0.6em;
  line-height: 1em;
  margin: 0;
}

.header-nav {
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  width: var(--content-width);
  padding: 0.4em 4em 0 4em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f5f5f5;
  border-bottom: 1px solid #ddd;
  font-size: var(--main-font-size);
}

.header-nav-item {
  font-size: 1.2em;
  line-height: 3.2em;
  color: #333 !important;
  text-decoration: none !important;
  padding: 0 1.2em;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  border: 1px solid transparent;
  border-bottom: none;
}

@media (max-width: 1280px) {
  .header-nav-item {
    font-size: 1em;
    line-height: 3em;
    padding: 0 1em;
  }
}

.header-nav-item.active,
.header-nav-item:hover {
  background-color: #fff;
  border-color: #ddd;
  position: relative;
}

.ant-dropdown-link.active {
  color: #40a9ff;
}

.header-nav-item.active::after,
.header-nav-item:hover::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background: #fff;
}

.page-container .post-container {
  min-height: 300px;
  width: var(--content-width);
  margin-left: var(--side-bar-width);
  padding: 6em 4em 4em 4em;
}

.post-container .inner {
  max-width: 60em;
  padding: 2em 4em;
  margin-top: 2em;
  border-radius: 5px;
  overflow: hidden;
}

.post-container .content {
  margin-top: 3.6em;
}

.post-container .article-menu {
  position: fixed;
  width: 20em;
  right: 10%;
  top: 11em;
  padding: 0.6em 0;
  background-color: #feffff;
  border: 1px solid #eee;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.post-container .article-menu .title-anchor.active {
  border-left: 2px solid #007fff;
}

@media (max-width: 1370px) {
  .post-container .article-menu {
    display: none;
  }
}

